import React from 'react'
import styled from 'styled-components'
export default function Background() {
	return (
		<Styled>
		</Styled>
		
	)
}

const Styled = styled.div`
	display: flex;
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background-color: #343434;
`
